import { create } from 'zustand';

type User = {
  uid: string;
  email: string | null;
  phoneNumber?: string | null;
  name: string;
  nickname?: string;
  countryCode?: string;
  uidCode?: string;
};

type Store = {
  user: User | null;
  setUser: (user: User | null) => void;
};

const useUserStore = create<Store>((set) => ({
  user: null,
  setUser: (user) => set({ user }),
}));

export default useUserStore;
